
import React, { useState } from 'react';
import Header from '../../components/feature/Header';
import Footer from '../../components/feature/Footer';

const Contact: React.FC = () => {
  const [selectedInquiryType, setSelectedInquiryType] = useState('');
  const [formData, setFormData] = useState({
    inquiryType: '',
    companyName: '',
    contactName: '',
    email: '',
    phone: '',
    position: '',
    companySize: '',
    industry: '',
    budget: '',
    timeframe: '',
    subject: '',
    message: ''
  });
  const [charCount, setCharCount] = useState(0);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    if (name === 'inquiryType') {
      setSelectedInquiryType(value);
    }

    if (name === 'message') {
      setCharCount(value.length);
    }
  };

  const handleRadioChange = (value: string) => {
    setSelectedInquiryType(value);
    setFormData(prev => ({
      ...prev,
      inquiryType: value
    }));
  };

  const handleFormSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      const response = await fetch('https://readdy.ai/api/form/d4f83uca42c0argjmch0', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: new URLSearchParams(formData).toString()
      });

      if (response.ok) {
        alert('문의가 성공적으로 전송되었습니다.');
        setFormData({
          inquiryType: '',
          companyName: '',
          contactName: '',
          email: '',
          phone: '',
          position: '',
          companySize: '',
          industry: '',
          budget: '',
          timeframe: '',
          subject: '',
          message: ''
        });
        setSelectedInquiryType('');
        setCharCount(0);
      } else {
        alert('전송 중 오류가 발생했습니다. 다시 시도해주세요.');
      }
    } catch (error) {
      console.error('Form submission error:', error);
      alert('전송 중 오류가 발생했습니다. 다시 시도해주세요.');
    }
  };

  return (
    <div className="min-h-screen">
      <Header />
      
      {/* Hero Section */}
      <section className="relative min-h-[70vh] flex items-center justify-center bg-gradient-to-br from-blue-600 to-purple-700">
        <div className="absolute inset-0 bg-cover bg-center bg-no-repeat"></div>
        <div className="absolute inset-0 bg-gradient-to-r from-blue-900/80 to-purple-900/70"></div>
        
        <div className="relative z-10 container mx-auto px-4 text-center text-white">
          <div className="max-w-4xl mx-auto">
            <h1 className="text-5xl md:text-6xl lg:text-7xl font-bold mb-8 leading-tight">문의하기</h1>
            <p className="text-xl md:text-2xl lg:text-3xl mb-12 text-white/90 max-w-3xl mx-auto leading-relaxed">
              스마트팩토리 구축부터 AI 기반 솔루션까지,<br />
              전문가와 함께 시작하세요
            </p>
          </div>
        </div>
        
        <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce">
          <i className="ri-arrow-down-line text-3xl text-white/70 w-8 h-8 flex items-center justify-center"></i>
        </div>
      </section>

      {/* Contact Form Section */}
      <section className="py-32 bg-white">
        <div className="container mx-auto px-4">
          <div className="text-center mb-20">
            <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-8">상세 문의하기</h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
              프로젝트에 대한 상세한 정보를 알려주시면 맞춤형 솔루션을 제안해드립니다
            </p>
          </div>

          <div className="max-w-5xl mx-auto">
            <div className="bg-white rounded-3xl p-12 shadow-xl border border-gray-100">
              <form id="contact-inquiry-form" className="space-y-10" onSubmit={handleFormSubmit} data-readdy-form>
                {/* Inquiry Type */}
                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-4">
                    문의 유형 <span className="text-red-500">*</span>
                  </label>
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    <label className="relative cursor-pointer">
                      <input
                        type="radio"
                        className="sr-only"
                        name="inquiryType"
                        value="consultation"
                        checked={selectedInquiryType === 'consultation'}
                        onChange={(e) => handleRadioChange(e.target.value)}
                      />
                      <div className={`p-6 border-2 rounded-xl text-center transition-all ${
                        selectedInquiryType === 'consultation'
                          ? 'border-blue-500 bg-blue-50'
                          : 'border-gray-200 hover:border-gray-300'
                      }`}>
                        <i className={`ri-question-answer-line text-3xl mb-3 block w-8 h-8 flex items-center justify-center mx-auto ${
                          selectedInquiryType === 'consultation' ? 'text-blue-600' : 'text-gray-400'
                        }`}></i>
                        <span className={`font-medium text-lg ${
                          selectedInquiryType === 'consultation' ? 'text-blue-700' : 'text-gray-700'
                        }`}>상담 문의</span>
                      </div>
                    </label>

                    <label className="relative cursor-pointer">
                      <input
                        type="radio"
                        className="sr-only"
                        name="inquiryType"
                        value="demo"
                        checked={selectedInquiryType === 'demo'}
                        onChange={(e) => handleRadioChange(e.target.value)}
                      />
                      <div className={`p-6 border-2 rounded-xl text-center transition-all ${
                        selectedInquiryType === 'demo'
                          ? 'border-blue-500 bg-blue-50'
                          : 'border-gray-200 hover:border-gray-300'
                      }`}>
                        <i className={`ri-play-circle-line text-3xl mb-3 block w-8 h-8 flex items-center justify-center mx-auto ${
                          selectedInquiryType === 'demo' ? 'text-blue-600' : 'text-gray-400'
                        }`}></i>
                        <span className={`font-medium text-lg ${
                          selectedInquiryType === 'demo' ? 'text-blue-700' : 'text-gray-700'
                        }`}>제품 데모</span>
                      </div>
                    </label>

                    <label className="relative cursor-pointer">
                      <input
                        type="radio"
                        className="sr-only"
                        name="inquiryType"
                        value="partnership"
                        checked={selectedInquiryType === 'partnership'}
                        onChange={(e) => handleRadioChange(e.target.value)}
                      />
                      <div className={`p-6 border-2 rounded-xl text-center transition-all ${
                        selectedInquiryType === 'partnership'
                          ? 'border-blue-500 bg-blue-50'
                          : 'border-gray-200 hover:border-gray-300'
                      }`}>
                        <i className={`ri-handshake-line text-3xl mb-3 block w-8 h-8 flex items-center justify-center mx-auto ${
                          selectedInquiryType === 'partnership' ? 'text-blue-600' : 'text-gray-400'
                        }`}></i>
                        <span className={`font-medium text-lg ${
                          selectedInquiryType === 'partnership' ? 'text-blue-700' : 'text-gray-700'
                        }`}>파트너십</span>
                      </div>
                    </label>
                  </div>
                </div>

                {/* Basic Information */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">
                      회사명 <span className="text-red-500">*</span>
                    </label>
                    <input
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg"
                      placeholder="회사명을 입력해주세요"
                      required
                      name="companyName"
                      value={formData.companyName}
                      onChange={handleInputChange}
                    />
                  </div>
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">
                      담당자명 <span className="text-red-500">*</span>
                    </label>
                    <input
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg"
                      placeholder="담당자명을 입력해주세요"
                      required
                      name="contactName"
                      value={formData.contactName}
                      onChange={handleInputChange}
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">
                      이메일 <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="email"
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg"
                      placeholder="이메일을 입력해주세요"
                      required
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                    />
                  </div>
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">
                      연락처 <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="tel"
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg"
                      placeholder="연락처를 입력해주세요"
                      required
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">직책</label>
                    <input
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg"
                      placeholder="직책을 입력해주세요"
                      name="position"
                      value={formData.position}
                      onChange={handleInputChange}
                    />
                  </div>
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">회사 규모</label>
                    <select
                      name="companySize"
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg pr-10"
                      value={formData.companySize}
                      onChange={handleInputChange}
                    >
                      <option value="">선택해주세요</option>
                      <option value="startup">스타트업 (1-10명)</option>
                      <option value="small">중소기업 (11-100명)</option>
                      <option value="medium">중견기업 (101-500명)</option>
                      <option value="large">대기업 (500명 이상)</option>
                    </select>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">업종</label>
                    <select
                      name="industry"
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg pr-10"
                      value={formData.industry}
                      onChange={handleInputChange}
                    >
                      <option value="">선택해주세요</option>
                      <option value="automotive">자동차</option>
                      <option value="electronics">전자/전기</option>
                      <option value="machinery">기계/장비</option>
                      <option value="chemical">화학/소재</option>
                      <option value="food">식품/음료</option>
                      <option value="textile">섬유/의류</option>
                      <option value="pharmaceutical">제약/바이오</option>
                      <option value="others">기타</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-lg font-medium text-gray-700 mb-3">예상 예산</label>
                    <select
                      name="budget"
                      className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg pr-10"
                      value={formData.budget}
                      onChange={handleInputChange}
                    >
                      <option value="">선택해주세요</option>
                      <option value="under-50m">5천만원 미만</option>
                      <option value="50m-100m">5천만원 ~ 1억원</option>
                      <option value="100m-300m">1억원 ~ 3억원</option>
                      <option value="300m-500m">3억원 ~ 5억원</option>
                      <option value="over-500m">5억원 이상</option>
                      <option value="consultation">상담 후 결정</option>
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-3">도입 일정</label>
                  <select
                    name="timeframe"
                    className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg pr-10"
                    value={formData.timeframe}
                    onChange={handleInputChange}
                  >
                    <option value="">선택해주세요</option>
                    <option value="immediate">즉시</option>
                    <option value="1-3months">1-3개월 이내</option>
                    <option value="3-6months">3-6개월 이내</option>
                    <option value="6-12months">6-12개월 이내</option>
                    <option value="over-1year">1년 이후</option>
                    <option value="flexible">유연하게 조정 가능</option>
                  </select>
                </div>

                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-3">
                    문의 제목 <span className="text-red-500">*</span>
                  </label>
                  <input
                    className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-lg"
                    placeholder="문의 제목을 입력해주세요"
                    required
                    name="subject"
                    value={formData.subject}
                    onChange={handleInputChange}
                  />
                </div>

                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-3">
                    문의 내용 <span className="text-red-500">*</span>
                  </label>
                  <textarea
                    name="message"
                    rows={8}
                    maxLength={500}
                    className="w-full px-6 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none text-lg"
                    placeholder="프로젝트 목표, 현재 상황, 기대하는 결과 등을 자세히 설명해주세요. 구체적인 정보를 제공해주시면 더 정확한 제안을 드릴 수 있습니다."
                    required
                    value={formData.message}
                    onChange={handleInputChange}
                  />
                  <div className="text-right text-sm text-gray-500 mt-2">{charCount}/500</div>
                </div>

                {/* Process Information */}
                <div className="bg-blue-50 p-8 rounded-xl">
                  <div className="flex items-start space-x-4">
                    <i className="ri-information-line text-blue-600 text-xl mt-1 w-6 h-6 flex items-center justify-center flex-shrink-0"></i>
                    <div className="text-blue-800">
                      <p className="font-medium mb-3 text-lg">문의 처리 절차</p>
                      <ul className="space-y-2 text-blue-700">
                        <li>• <strong>1단계:</strong> 문의 접수 및 담당자 배정 (당일)</li>
                        <li>• <strong>2단계:</strong> 현황 분석 및 솔루션 검토 (1-2일)</li>
                        <li>• <strong>3단계:</strong> 맞춤형 제안서 작성 (3-5일)</li>
                        <li>• <strong>4단계:</strong> 상담 일정 조율 및 제안 (5-7일)</li>
                      </ul>
                    </div>
                  </div>
                </div>

                <button
                  type="submit"
                  className="w-full bg-blue-600 text-white py-6 rounded-xl hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors whitespace-nowrap cursor-pointer font-semibold text-xl inline-flex items-center justify-center gap-4"
                >
                  문의하기
                  <i className="ri-send-plane-fill text-2xl w-6 h-6 flex items-center justify-center"></i>
                </button>
              </form>
            </div>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
};

export default Contact;
